.container {
  height: 60px;
  width: 100%;
  /* border: 2px solid black; */
  background-color: white;
  display: flex;
  align-items: center;
  color: rgb(52, 52, 52);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  box-sizing: border-box;

  .navanimate {
    position: absolute;
    height: 5px;
    width: 10px;
    background-color: red;
    bottom: 0;
    animation-name: exam;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    left: 0;
    animation-timing-function: linear;
    border-radius: 8px;
  }
  @keyframes exam {
    0% {
      background-color: red;
      left: 0px;
      width: 10px;
    }
    25% {
      background-color: rgb(3, 134, 131);
      left: 50%;
      width: 200px;
    }

    50% {
      background-color: rgb(239, 16, 5);
      left: 100%;
      width: 10px;
    }
    75% {
      background-color: rgb(228, 236, 14);
      left: 50%;
      width: 200px;
    }

    100% {
      background-color: red;
      left: 0px;
      width: 10px;
    }
  }
}

.container ul {
  text-decoration: none;
  list-style-type: none;
  display: flex;
  font-weight: bold;
}
.container a {
  color: black;
}
.logo {
  font-size: 30px;
  margin: 10px;
  font-weight: bold;
  width: 20%;
  color: rgb(47, 47, 47);
  font-family: "Pacifico", cursive;
}
#checker {
  display: none;
}

.fa-bars {
  display: none;
  margin: 10px;
}

.container ul li {
  padding:15px 20px;
  position: relative;

  div {
    height: 100%;
    width: 100%;
    position: absolute;
    
    top: 0;
    left: 0;
    z-index: -1;
    transition: background-image 1s;
  }
}
.container ul li:hover {
    color: white;
    a{
        color: white;
    }
  div {
    animation-name: pidda;
    animation-duration: 0.7s;
    background-image: linear-gradient(-45deg,blue, skyblue);
    background-size: 200%;
    border-radius: 4px;
    // animation-iteration-count: 1;
    @keyframes pidda {
      from  {
        background-image: white;
        width: 0%;
      }
    //   25% {
    //     background-color: rgb(3, 134, 131);
    //     left: 50%;
    //     width: 100%;
    //   }

    //   50% {
    //     background-color: rgb(239, 16, 5);
    //     left: 100%;
    //     width: 0%;
    //   }
    //   75% {
    //     background-color: rgb(228, 236, 14);
    //     left: 50%;
    //     width: 100%;
    //   }

      to {
        background-image: linear-gradient(-45deg,blue, skyblue);
        width: 100%;
      }
    }
  }
}

label{
    position: absolute;
    right: 0px;
}

@media only screen and (max-width: 600px) {
  .fa-bars {
    display: block;
    font-size: 40px;
  }
  .container {
    justify-content: space-between;
    box-sizing: border-box;
  }
  ul {
    position: fixed;
    top: 60px;
    left: -50%;
    flex-direction: column;
    align-items: center;
    width: 50%;
    background-color: white;
    height: 100vh;
    transition: all 0.5s ease-in;
    box-sizing: border-box;
    z-index: 5;
  }
  #checker:checked ~ ul {
    left: 0;
  }
}
