$BG1: linear-gradient(-45deg, blue, skyblue);

@mixin flex-box($align-item:flex-start,$flex-direction:row,$justify-content: center){
display: flex;
align-items: $align-item;
flex-direction: $flex-direction;
justify-content: $justify-content;
}